import React, { Component } from "react";
import "./Login.css";
import axios from "axios";
import authService from "../../services/auth-service";
//import axiosService from '../../services/axios-service';
import { Redirect } from "react-router-dom";
import { API_URL } from "../../services/url";
class AddLogin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      contact: "",
      password: "",
      lessthanfive: 0,
      betweenfiveandten: 0,
      betweentenandtwenty: 0,
      bewtweentwentyandfifty: 0,
      betweenfiftyandhundred: 0,
      morethanhundred: 0
    };
  }
  change = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  onSubmit = async e => {
    e.preventDefault();
    this.setState({
      contact: "",
      password: ""
    });
    //console.log(this.state);
    //console.log(`${API_URL}/company/login`);
    await axios.post(`${API_URL}/admin/loginDo`, this.state).then(res => {
      console.log(res);
      if (res.status === 200) {
        console.log(res.data);
        console.log(res.data.companyEquipmentMap.length);
        //   const newcount = res.data.companyEquipmentMap.map(x => x.count)
        // console.log(newcount)
        // console.log(res.data.companyEquipmentMap.map(x => x.count))
        for (let i = 0; i < res.data.companyEquipmentMap.length; i++) {
          console.log(res.data.companyEquipmentMap[i].count);
          if (res.data.companyEquipmentMap[i].count <= 5) {
            this.setState({
              lessthanfive: ++this.state.lessthanfive
            });
          }
          // console.log("ess than fove hree", this.state.lessthanfive);
          localStorage.setItem("lessthanfive", this.state.lessthanfive);
          //netween 5 nad 10
          if (
            res.data.companyEquipmentMap[i].count > 5 &&
            res.data.companyEquipmentMap[i].count < 10
          ) {
            this.setState({
              betweenfiveandten: ++this.state.betweenfiveandten
            });
          }
          // console.log("between 5 and 10 ", this.state.betweenfiveandten);
          localStorage.setItem(
            "betweenfiveandten",
            this.state.betweenfiveandten
          );
          //10 and 20
          if (
            res.data.companyEquipmentMap[i].count > 10 &&
            res.data.companyEquipmentMap[i].count < 20
          ) {
            this.setState({
              betweentenandtwenty: ++this.state.betweentenandtwenty
            });
          }
          // console.log("between 10 and 20 ", this.state.betweentenandtwenty);
          localStorage.setItem(
            "betweentenandtwenty",
            this.state.betweentenandtwenty
          );

          //20and 50
          if (
            res.data.companyEquipmentMap[i].count > 20 &&
            res.data.companyEquipmentMap[i].count < 50
          ) {
            this.setState({
              bewtweentwentyandfifty: ++this.state.bewtweentwentyandfifty
            });
          }
          // console.log("between 20 and 50 ", this.state.bewtweentwentyandfifty);
          localStorage.setItem(
            "bewtweentwentyandfifty",
            this.state.bewtweentwentyandfifty
          );

          //50 and 100
          if (
            res.data.companyEquipmentMap[i].count > 50 &&
            res.data.companyEquipmentMap[i].count < 100
          ) {
            this.setState({
              betweenfiftyandhundred: ++this.state.betweenfiftyandhundred
            });
          }
          // console.log("between 50 and 100 ", this.state.betweenfiftyandhundred);
          localStorage.setItem(
            "betweenfiftyandhundred",
            this.state.betweenfiftyandhundred
          );

          //more than 100
          if (res.data.companyEquipmentMap[i].count > 100) {
            this.setState({
              morethanhundred: ++this.state.morethanhundred
            });
          }
          // console.log("more than 100 ", this.state.morethanhundred);
          localStorage.setItem("morethanhundred", this.state.morethanhundred);
        }
        //outputs
        console.log(
          "less than aur equal to 5 (including 1)",
          this.state.lessthanfive
        );
        console.log("between 5 and 10 ", this.state.betweenfiveandten);
        console.log("between 10 and 20 ", this.state.betweentenandtwenty);
        console.log("between 20 and 50 ", this.state.bewtweentwentyandfifty);
        console.log("between 50 and 100 ", this.state.betweenfiftyandhundred);
        console.log("more than 100 ", this.state.morethanhundred);

        //   const equipmentcount = res.data.companyEquipmentMap
        //   console.log(equipmentcount.map(function(item,_id)
        // {
        //   return (
        //    <p> {item.count}</p>
        //   )
        // }
        // ))
        authService.saveToken(res.data.w_auth);
        localStorage.setItem("companyId", res.data.companyId);
        localStorage.setItem("number", res.data.user.contactNumber);
        localStorage.setItem("name", res.data.user.name);
        localStorage.setItem("sucatagoryCount", res.data.sucatagoryCount);
        localStorage.setItem("companyCount", res.data.companyCount);
        localStorage.setItem("equipmentCount", res.data.equipmentCount);
        localStorage.setItem("requirementCount", res.data.requirementCount);
        localStorage.setItem("feedbackCount", res.data.feedbackCount);
        // localStorage.setItem('companyEquipmentMap', res.data.companyEquipmentMap)
        let usr = res.data.user;
        let c = usr.company;
        //console.log(c)

        localStorage.setItem("company_yantra_client", c.name);
        //  if(res.data.loginSucces){
        // alert("Login Successful");
        // this.props.onRouteChange('home');
        // return <Redirect to={{pathname: '/'}} />
         window.location.reload();
      }
       else {
        alert(res.data.message);
      }
    
    });
  };
  render() {
    //const onRouteChange = this.props.onRouteChange;
    return (
      <main className="main">
        <div className="form-example-area custom-form-example">
          <div class="container">
            <div class="row justify-content-center">
              <div class="col-md-8">
                <div class="card-group">
                  <div class="card p-4 custom-login-card">
                    <div class="card-body">
                      <h1>Login</h1>
                      <p class="text-muted">Sign In to your account</p>
                      <div class="input-group mb-3">
                        <div class="input-group-prepend">
                          <span class="input-group-text">
                            <i class="icon-user" />
                          </span>
                        </div>
                        <input
                          class="form-control"
                          type="text"
                          placeholder="Enter Mobile Number"
                          name="contact"
                          value={this.state.contact}
                          onChange={e => this.change(e)}
                        />
                      </div>
                      <div class="input-group mb-4">
                        <div class="input-group-prepend">
                          <span class="input-group-text">
                            <i class="icon-lock" />
                          </span>
                        </div>
                        <input
                          class="form-control"
                          type="password"
                          placeholder="Enter Password"
                          name="password"
                          value={this.state.password}
                          onChange={e => this.change(e)}
                        />
                      </div>
                      <div class="row">
                        <div class="col-6">
                          <button
                            class="btn btn-primary px-4"
                            onClick={e => this.onSubmit(e)}
                            type="button"
                          >
                            Login
                          </button>
                        </div>
                        {/* <div class="col-6 text-right">
                    <button class="btn btn-link px-0" type="button">Forgot password?</button>
                  </div> */}
                      </div>
                    </div>
                  </div>
                  {/* <div class="card text-white bg-primary py-5 d-md-down-none" style={{width:"44%"}}>
              <div class="card-body text-center">
                <div>
                  <h2>Sign up</h2>
                  <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                  <button class="btn btn-primary active mt-3" type="button">Register Now!</button>
                </div>
              </div>
            </div> */}
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
    );
  }
}

export default AddLogin;
